import { Box, Text, Image, useColorModeValue } from "@chakra-ui/react";

const props = [
  {
    image: "https://img.icons8.com/color/512/html-5.png",
    text: "HTML",
  },
  {
    image: "https://img.icons8.com/color/512/css3.png",
    text: "CSS",
  },
  {
    image: "https://img.icons8.com/fluency/512/javascript.png",
    text: "JavaScript",
  },
  {
    image: "https://img.icons8.com/fluency/512/typescript--v2.png",
    text: "TypeScript",
  },
  {
    image:
      "https://manojattri.vercel.app/static/media/react.0cf951a69d8e58f83f9d.png",
    text: "Reat JS",
  },
  {
    image: "https://img.icons8.com/color/512/chakra-ui.png",
    text: "Chakra UI",
  },
  // {
  //   cls:style.html,
  //   image:'https://img.icons8.com/color/512/material-ui.png',
  //   text:'Material UI'
  // },
  {
    image: "https://img.icons8.com/color/512/redux.png",
    text: "Redux",
  },
  {
    image: "https://img.icons8.com/fluency/512/node-js.png",
    text: "Node JS",
  },
  {
    image: "https://img.icons8.com/color/512/mongodb.png",
    text: "Mongo DB",
  },
  {
    image: "https://amansingh456.github.io/static/media/express.39a0f4c3.png",
    text: "Express",
  },

  {
    image: "https://amansingh456.github.io/static/media/github.3b9e3290.png",
    text: "Git Hub",
  },
  {
    image: "https://img.icons8.com/fluency/512/visual-studio-code-2019.png",
    text: "VS Code",
  },
  {
    image: "https://img.icons8.com/color/512/npm.png",
    text: "npm",
  },
];

function Skills() {
  const bgColorValue = useColorModeValue("white", "gray.800");
  const colorValue = useColorModeValue("gray.800", "white");
  return (
    <Box bg={useColorModeValue("gray.100", "gray.800")} pb="45px" id="skills">
      <Text
        textAlign="center"
        fontSize="4xl"
        fontWeight="bold"
        mb={-1}
        color="rgb(237,137,54)"
      >
        Skills
      </Text>
      <Box
        m="auto"
        display="grid"
        gridTemplateColumns={[
          "repeat(2,1fr)",
          "repeat(3,1fr)",
          "repeat(3,1fr)",
          "repeat(3,1fr)",
          "repeat(3,1fr)",
        ]}
        pt={9}
        px={9}
        maxW="5xl"
        margin={"auto"}
        rowGap={10}
        columnGap={10}
      >
        {props.map(({ cls, image, text }) => (
          <Box
            key={text}
            className='skills-card'
            margin="auto"
            height="160px"
            textAlign="center"
            padding={4}
            transition=" all 0.5s ease-in-out"
            boxShadow="rgb(227, 77, 38) 0px 5px 15px"
            w={{ base: "135px", md: "200px", lg: "250px" }}
            rounded="lg"
            p={4}
            pr={6}
            py={5}
            bg={bgColorValue}
            _hover={{ transform: "scale(0.9)" }}
          >
            <Image
              m={"auto"}
              w={{ base: "90px" }}
              src={image}
              className="skills-card-img"
            />
            <Text
              fontWeight="semibold"
              color={colorValue}
              mt={4}
              className="skills-card-name"
            >
              {text}
            </Text>
          </Box>
        ))}
      </Box>
    </Box>
  );
}

export default Skills;
